* {
  margin: 0;
  box-sizing: border-box;
}

//styling

html {
  font-family: "Nunito Sans", sans-serif;
  font-size: 16px;
  overflow-x: hidden;
  background-color: $black-900;
  color: $white;
}
//!for mobile menu and hide desktop menu
.top-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: $shadow-black-transparent;
  backdrop-filter: blur(0.5rem);
  width: 100%;
  height: 3.5rem;
  padding: 0 1rem;
  position: fixed;
  z-index: 2;
}
#logo,
#dropdown-menu-icon {
  display: block;
  width: 1.75rem;
  height: 1.75rem;
  font-size: 1.75rem;
  transition: ease-in-out 0.2s;
  cursor: pointer;
  &:hover {
    color: $grey-500;
  }
}
#chevron-to-show-social-menu {
  display: none;
}
.nav-desktop {
  display: none;
}

//toggle menu

.toggle-menu {
  display: none;
  position: fixed;
  width: 100%;
  height: auto;
  background-color: $black-600;
  transform: translateY(3.5rem);
  color: $grey-500;

  border-radius: 0 0 5% 5%;
  box-shadow: 1px 3px 15px $shadow-black-transparent;
  opacity: 0;
}

.toggle-menu.active {
  transition: 0.2s ease-in-out;
  opacity: 1;
  display: block;
  z-index: 2;
}

.all-inside {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;
  padding: 1rem;
}

.toggle_menu-a {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem 2rem;
  font-size: 1.2rem;
  text-transform: uppercase;
  font-weight: 800;
}
.nav-a-toggle-menu {
  text-decoration: none;
  color: $amulet-green-400;
  transition: 0.1s ease-in-out;
  &:hover {
    transform: scale(1.05);
    color: $grey-600;
  }
}

//right side toggle menu
.toggle-column-right {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: auto;
  gap: 0.5rem;
}

#toggle-menu-a-1,
#toggle-menu-a-2,
#toggle-menu-a-3,
#toggle-menu-a-4,
#toggle-menu-a-5,
#toggle-menu-a-6,
#toggle-menu-a-7,
#toggle-menu-a-8,
#toggle-menu-a-9 {
  color: $white;
  font-size: 1.75rem;
  transition: 0.1s ease-in-out;

  &:hover {
    transform: scale(1.2);
    color: $grey-500;
  }
}

/* bottom navigation */
.bottom-navigation-only {
  height: 49.8%;
  width: 100vw;
}

.bottom-nav-ul {
  display: flex;
  justify-content: left;
  align-items: center;
  flex-direction: row;
  list-style: none;
  font-size: 0.9rem;
  padding: 0 1.5rem;
  width: 100vw;
  height: auto;
}

.bottom-li-nav {
  margin: 1rem;
  gap: 1.5rem;
}

#bottom-li-nav-about,
#bottom-li-nav-jobs,
#bottom-li-nav-appren {
  cursor: pointer;
}

#bottom-li-nav-about {
  color: $amulet-green-400;
}
.nav-a-social {
  display: none;
}
//section 1

.section-1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: auto;
  padding: 4rem 0.5rem 0 0.5rem;
}

.section-1-card {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  width: 100%;
  height: auto;
  padding: 2rem 1rem;
  background-color: $shadow-black;
  border-radius: 1rem;
}
//name and logo
.link-to-story {
  text-decoration: none;
  color: $white;
  cursor: pointer;
}

.name-and-logo {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
  width: 100%;
  height: auto;
  gap: 1.3rem;
  line-height: 3.8rem;
}
.h1-main-name {
  color: $white;
  font-weight: 800;
  width: auto;
  font-size: 1.9rem;
  &:hover,
  :focus,
  :active {
    color: $eastern-blue-500;
  }
}
.img-square-section {
  width: 1.9rem;
  height: 1.9rem;
}

//introduction
.dot-and-introduction {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 0.5rem;
  line-height: 1.6rem;
}
.p_introduction {
  display: flex;
  justify-items: center;
  align-items: center;
  font-size: 0.8rem;
  color: $grey-600;
}
#dot-introduction {
  color: $amulet-green-400;
  font-size: 0.6rem;
}
//main description

.p_description {
  line-height: 2rem;
  font-size: 1rem;
  font-weight: 500;
}
.hr-line-to-p {
  border: none;
  width: 100%;
  background-color: $grey-700;
  height: 0.05rem;
  opacity: 0.1;
  margin: 0.5rem 0;
}

.h2_description {
  line-height: 2.8rem;
  font-size: 1.4rem;
  font-weight: 700;
}
.name-in-p-green {
  color: $amulet-green-400;
  font-weight: 700;
}
.name-in-p-blue {
  color: $eastern-blue-500;
  font-weight: 700;
}

//btn discover story
.discover-div {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: auto;
  background-color: transparent;
}
.btn-discover {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  background-color: $grey-700;
  color: $white;
  width: 14rem;
  height: auto;
  margin: 1rem 0;
  font-size: 1rem;
  font-weight: 700;
  transition: 0.1s ease-in-out;

  &:hover,
  :focus,
  :active {
    outline: none;
    transform: scale(1.1);
    background-color: $grey-600;
    color: $black-900;
  }
}
.hr-line-to-sections {
  border: none;
  width: 100%;
  background-color: $grey-700;
  height: 0.05rem;
  opacity: 0.1;
  margin-top: 1rem;
}

// section 2 new albums with section 4 latest singles
.section-2,
.section-4 {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: auto;
  padding: 2rem 1rem;
  gap: 0.5rem;
}

.h-section-2,
.h-section-4 {
  font-size: 1.4rem;
  text-transform: uppercase;
  width: 100%;
  margin-bottom: 2rem;
  font-weight: 700;
}
.name-tag-blue {
  color: $eastern-blue-500;
}
.name-tag-green {
  color: $amulet-green-400;
}
.link-to-all-collection {
  text-decoration: none;
  color: $white;
  transition: 0.1s ease-in-out;
}
//albums thing
.albums-chooser,
.singles-chooser {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  width: 100%;
  height: auto;
  min-height: 20rem;
  gap: 1.5rem;
  z-index: 1;
}

//test nowy kod
.album-cover,
.single-cover {
  width: 7.5rem;
  height: auto;
  min-height: 7.5rem;
  min-width: 7.5rem;
  border-radius: 5%;
}
.album,
.single {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: column;
  width: 7.5rem;
  height: auto;
  min-height: 13rem;
  transition: 0.1s linear;
  cursor: pointer;
  &:hover {
    transform: scale(1.1);
  }
}
.song-title-small {
  overflow: hidden;
  text-overflow: ellipsis;
  font-weight: 600;
  font-size: 0.9rem;
  white-space: wrap;
  width: 100%;
  margin-top: 0.5rem;
}

.content-small {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: 0.2rem;
  width: 100%;
  height: auto;
  margin-top: auto;
}

.date-small {
  font-weight: 400;
  font-size: 0.9rem;
}

.dots-separator-album,
.dots-separator-single {
  color: $eastern-blue-500;
  font-size: 1.4rem;
}

//end test

// a - for mini albums
.album-a-next-page {
  text-decoration: none;
  color: $white;
}
